import React, { useState, useEffect } from "react";
import { TextField, Button } from "@material-ui/core/";
import { useHistory, Link } from "react-router-dom";
import db, { firebaseApp, auth } from "../Firebase/firebase";
function Homepage() {
  const history = useHistory();

  const [foodlist, setFoodlist] = useState([]);

  useEffect(() => {
    firebaseApp.auth().onAuthStateChanged((user1) => {
      db.collection("users")
        .doc(user1.uid)
        .collection("Food Item")
        .onSnapshot((snapshot) => {
          setFoodlist(
            snapshot.docs.map((doc) => ({
              id: doc.id,
              data: doc.data(),
            }))
          );
          // snapshot.docs.map((doc) => {
          //   console.log("Data", doc.data());
          // });
        });
    });
  }, []);

  useEffect(() => {
    console.log("Food List", foodlist);
  }, [foodlist]);

  return (
    <div style={{ margin: "5% 0% 0% 0% " }}>
      <Button
        type="submit"
        className="loginbutton1"
        variant="contained"
        color="primary"
        onClick={() => {
          history.push("/addFood");
        }}
      >
        {" "}
        Add Food Item
      </Button>
      <br />
      <br />
      <h3 style={{ margin: "1% 0% 3% 0%" }}>Food List</h3>
      <div>
        <table className="table">
          <thead>
            <th>ID</th>
            <th>Name</th>
            <th>Price</th>
            <th>Image</th>
          </thead>
          <tbody>
            {foodlist.map((food) => {
              return (
                <tr id={food.id}>
                  <td>{food.data.FoodID}</td>
                  <td>{food.data.FoodName}</td>
                  <td>{food.data.FoodPrice}</td>
                  <td>
                    <img
                      src={food.data.FoodImage}
                      alt="No Image"
                      width="100px"
                      height="100px"
                    />
                  </td>
                </tr>
              );
            })}
          </tbody>
        </table>
      </div>
    </div>
  );
}

export default Homepage;
