import React, { useState, useEffect } from "react";
import { TextField, Button } from "@material-ui/core/";
import db, { firebaseApp, auth } from "../Firebase/firebase";
import { useHistory, Link } from "react-router-dom";

function AddFoodItem() {
  const [image, setImage] = useState("");
  const [message, setMessage] = useState("");

  const [inputFields, setInputFields] = useState({
    id: "",
    fname: "",
    price: "",
  });
  const history = useHistory();

  const changeHandler = (event) => {
    const { name, value } = event.target;
    setInputFields({ ...inputFields, [name]: value });
  };

  useEffect(() => {
    console.log("Input Value", image);
  }, [image]);

  const imageUpload = (e) => {
    // console.log("Inside onChange function");

    let files;
    if (e.dataTransfer) {
      files = e.dataTransfer.files;
      console.log("Files", files);
    } else if (e.target) {
      files = e.target.files;
      console.log("Files", files);
    }
    if (files[0] !== "undefined") {
      const reader = new FileReader();
      reader.onload = () => {
        setImage(reader.result);
        // console.log(reader.result);
      };
      reader.readAsDataURL(files[0]);
    }

    // e.target.value = null;
  };

  const clickHandler = (e) => {
    e.preventDefault();

    if (inputFields.id && inputFields.price && inputFields.fname && image) {
      firebaseApp.auth().onAuthStateChanged((user1) => {
        db.collection("users")
          .doc(user1.uid)
          .collection("Food Item")
          .doc(inputFields.id)
          .set({
            FoodID: inputFields.id,
            FoodName: inputFields.fname,
            FoodPrice: inputFields.price,
            FoodImage: image,
          });
      });
    } else {
      setMessage("**Enter all fields");
    }
  };

  return (
    <div>
      <Button
        //className="align-left"
        variant="contained"
        color="primary"
        style={{ marginTop: "50px", marginBottom: "50px" }}
        onClick={() => {
          history.push("/home");
        }}
      >
        Go Back
      </Button>
      <div
        style={{
          margin: "0% 25% 5% 20%",
          padding: "5%",
          border: "5px solid black",
        }}
      >
        <h3>Add Food Item</h3>

        <form noValidate autoComplete="off" onSubmit={clickHandler}>
          <TextField
            type="number"
            name="id"
            className="textfield"
            id="standard-basic"
            label="Food ID"
            onChange={changeHandler}
          />

          <br></br>
          <TextField
            type="text"
            name="fname"
            className="textfield"
            id="standard-basic"
            label="Food Name"
            onChange={changeHandler}
          />
          <br></br>
          <TextField
            type="number"
            name="price"
            className="textfield"
            id="standard-basic"
            label="Price"
            onChange={changeHandler}
          />
          <br></br>
          <br></br>
          {image ? (
            <img src={image} alt="no image" width="5%" height="5%" />
          ) : null}

          <TextField
            style={{ width: "35%" }}
            type="file"
            name="image"
            //className="textfield"
            //id="standard-basic"
            //label="Image"
            onChange={imageUpload}
          />
          <br></br>

          <br></br>

          <Button
            type="submit"
            className="loginbutton1"
            color="default"
            variant="contained"
          >
            Add Food Item
          </Button>
          <br></br>
          <label className="text-danger">{message}</label>
        </form>
      </div>
    </div>
  );
}

export default AddFoodItem;
